.amba

  &__inner
    padding: 90px 50px 90px 50px
    max-width: 1700px
    box-sizing: content-box
    margin: auto

  &__h2
    font-size: $h2-size
    text-align: center
    font-weight: 400
    margin: 0
    margin-bottom: 90px

  &__h3
    font-size: 54px
    margin: 0
    margin-top: 90px
    margin-bottom: 60px
    text-align: center

  &__h4
    font-size: 32px
    font-weight: 700
    margin: 0
    margin-top: 48px

  &__p
    font-size: 32px
    margin: 0
    margin-top: 24px

  &__ol
    margin-left: 12px

  &__li
    font-size: 32px
    margin-top: 18px

  &__pics
    display: flex
    gap: 5%
    align-items: center
    margin-top: 90px
    margin-bottom: 90px

  &__qr-wrapper
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    gap: 60px
    width: 30%

  &__qr
    max-width: 290px
    width: 80%

  &__three
    height: 100%
    overflow: hidden
    width: 65%

  & .btn
    max-width: 100%
    background-position: 15%
    padding: 10% 12% 10% 30%
    font-size: 150%
    background-size: 12%




@media screen and (max-width: 1600px)
  .amba
    &__inner
      max-width: 1240px
      box-sizing: border-box

    &__h2 
      font-size: 60px
      margin-bottom: 40px
      
    &__h3
      font-size: 32px
      margin-bottom: 12px

    &__h4
      font-size: 24px
      margin-top: 42px
      margin-bottom: 10px

    &__h4:first-of-type
      margin-top: 0

    &__p
      font-size: 21px
      line-height: 1.35
      margin-top: 0px



@media screen and (max-width: 760px)
  .amba
    &__inner
      padding: 50px 30px

    &__h2
      font-size: $h2-768
      margin-top: 0
      margin-bottom: 42px

    &__p
      font-size: $p-768
      margin-top: 12px
      margin-bottom: 42px


@media screen and (max-width: 480px)
  .amba
    &__inner
      padding: 10px 10px 34px 10px

    &__screen
      border-radius: 0

    &__h2
      font-size: $h2-480
      margin-top: 12px
      margin-bottom: 12px
    
    &__h3
      font-size: 15px
      margin-top: 24px

    &__h4
      font-size: 12px
      margin-top: 18px

    &__p
      font-size: $p-480
      margin-bottom: 6px
      line-height: 12px

    &__ul,
    &__ol
      padding-left: 15px

    &__screenshot
      margin: 20px 0

    &__mini-game
      margin-top: 30px

    &__dl-li
    margin-top: 14px
    line-height: 10px

    &__dt,
    &__dd
      font-size: $p-480

    &__dl
      margin-bottom: 0

    &__columns
      gap: 0

    &__li
      margin-top: 12px