.socials
  background-color: transparent
  max-width: fit-content
  z-index: 9

  &__list
    padding: 0
    margin: 0
    display: flex
    flex-direction: column
    gap: 14px
    list-style-type: none

  &__item
    width: 64px
    height: 64px
    border-radius: 50%

    &:nth-of-type(1) .socials__link
      background-image: url('img/icon-socials--telegram.svg')

    &:nth-of-type(2) .socials__link
      background-image: url('img/icon-socials--x.svg')

    &:nth-of-type(3) .socials__link
      background-image: url('img/icon-socials--dextools.svg')

    &:nth-of-type(4) .socials__link
      background-image: url('img/icon-socials--dexscreener.svg')

    &:nth-of-type(5) .socials__link
      background-image: url('img/icon-socials--coingecko.svg')

    &:nth-of-type(6) .socials__link
      background-image: url('img/icon-socials--binance.svg')

    &:nth-of-type(7) .socials__link
      background-image: url('img/icon-socials--mexc.svg')

    &:nth-of-type(8) .socials__link
      background-image: url('img/icon-socials--coinmarketgap.svg')

    &:nth-of-type(9) .socials__link
      background-image: url('img/icon-socials--coinmarketgap.svg')

  &__link
    position: relative
    display: inline-block
    width: 100%
    height: 100%
    background-color: #000000
    background-size: 100%
    background-repeat: no-repeat
    background-position: center
    border-radius: 50%


    &:hover
      @media #{$mouse-device}
        background-color: $orange
        transition: 0.3s all ease
        transform: scale(1.15)

    &:active
        background-color: $orange
        transition: 0.3s all ease




@media screen and (max-width: 1920px)
  .socials
    &__item
      width: 46px
      height: 46px


@media screen and (max-width: 960px)
  .socials
    bottom: 20px

    &__list
      gap: 12px
      
    &__item
      width: 42px
      height: 42px



@media screen and (max-width: 760px)
  .socials
    right: 15px
    bottom: 15px

    &__list
      flex-direction: row
      width: 100vw
      gap: 20px
      justify-content: flex-end

    &__item
      width: 38px
      height: 38px


@media screen and (max-width: 490px)
  .socials
    right: 0

    &__list
      flex-direction: row
      width: 100vw
      gap: initial
      justify-content: space-around


@media screen and (max-width: 420px)
  .socials
    &__item
      width: 34px
      height: 34px


@media screen and (max-width: 360px)
  .socials
    &__item
      width: 32px
      height: 32px


@media screen and (max-width: 360px)
  .socials
    bottom: 10px

    &__item
      width: 30px
      height: 30px
      