.history
  font-family: $unb
  background: linear-gradient(180deg, #7CD1EC, #EFE8E8)
  background-size: 100% 750px
  background-repeat: no-repeat

  &__inner
    padding: 90px 50px 90px 50px
    max-width: 1700px
    margin: auto

  &__h2
    font-size: $h2-size
    color: $black
    text-align: center
    font-weight: 400
    margin-top: 0
    margin-bottom: 0

  &__p
    font-size: $p-size
    color: $black
    font-weight: 400
    margin-top: 70px
    margin-bottom: 70px
    line-height: 1.45

  &__screenshot
    display: flex
    gap: 6%
    justify-content: space-between
    align-content: center
    max-width: 90%
    margin: auto
    margin-bottom: 0

    &::last-of-type
      margin-bottom: 0

  &__phone-wrapper
    position: relative
    z-index: 0
    background-image: url('img/phone.svg')
    background-repeat: no-repeat
    background-size: contain
    max-width: 426px
    width: 100%
    aspect-ratio: 426 / 761
    display: flex
    align-items: center
    justify-content: center

  &__btn-play
    position: absolute
    width: 235px
    height: 235px
    background-repeat: no-repeat
    background-size: contain
    position: absolute
    z-index: 99999
    transition: all 0.3s ease
    border: none
    background-color: transparent

    &:hover
      @media #{$mouse-device}
        transition: all 0.3s ease
        transform: scale(1.1)
        cursor: pointer

    &:active
      transition: all 0.3s ease
      transform: scale(1.1)
      cursor: pointer
      
    &:nth-of-type(1)
      background-image: url('img/btn__play--black.svg')


  &__screen
    position: absolute
    top: 0
    left: 0
    z-index: 999
    max-width: 426px
    width: 100%
    height: auto
    border-radius: 0


@media screen and (max-width: 1600px)
  .history

    &__inner
      max-width: 1240px

    &__h2 
      font-size: 60px

    &__p
      font-size: 24px
      color: #000000
      font-weight: 400
      margin-top: 50px
      margin-bottom: 50px



@media screen and (max-width: 1024px)
  .history
    &__h2
      font-size: $h2-1024

    &__p
      font-size: $p-1024
      margin-bottom: 40px

    &__screenshot
      margin-bottom: 70px

      &:last-of-type
        margin-bottom: 0


@media screen and (max-width: 760px)
  .history
    &__inner
      padding: 50px 30px

    &__h2
      font-size: $h2-768
      margin-top: 0
      margin-bottom: 42px

    &__p
      font-size: $p-768
      margin-bottom: 42px
      
    &__screenshot
      margin-bottom: 42px


  
@media screen and (max-width: 480px)
  .history
    &__inner
      padding: 10px 10px 34px 10px

    &__h2
      font-size: $h2-480
      margin-top: 12px
      margin-bottom: 24px

    &__p
      font-size: $p-480
      margin-bottom: 18px
      
    &__screenshot
      margin-bottom: 18px