.header
  position: relative
  padding: 40px
  margin: auto
  background-color: $blue

  &__inner
    display: flex
    justify-content: space-between
    align-items: center
    max-width: 1420px
    margin: auto

  &__logo
    padding-right: 40px

  &__nav
    padding-left: 40px
    display: flex
    justify-content: space-between
    align-items: center
    width: 100%
    height: 40px
    border-left: 2px solid $white

  &__links
    display: flex
    justify-content: space-between
    list-style-type: none
    padding-left: 0
    width: 100%
    margin: 0
    align-items: baseline

  &__link
    color: $white
    text-transform: uppercase
    font-weight: 700
    text-decoration: none
    font-size: 24px
    line-height: 1
    border-bottom: 2px solid $blue

    &:hover
      @media #{$mouse-device}
        transition: all 0.3s ease
        border-bottom: 2px solid $white

    &:active
      border-bottom: 2px solid $white

  &__btn
    background-repeat: no-repeat
    background-color: transparent
    background-size: cover
    width: 27px
    height: 24px
    border: none


@media screen and (max-width: 1920px)
  .header
    padding: 40px 40px 50px 40px



@media screen and (max-width: 1360px)
  .header
    padding: 30px 

    &__logo
      width: 200px
      box-sizing: content-box

    &__link
      font-size: 18px


@media screen and (max-width: 990px)
  .header
    padding: 20px 30px

    &__logo
      width: 200px
      box-sizing: content-box

    &__link
      font-size: 14px


@media screen and (max-width: 860px)
  .header
    padding: 15px 25px

    &__nav
      padding-left: 20px
    
    &__logo
      width: 142px
      padding-right: 25px


@media screen and (max-width: 760px)
  .header
    &__nav
      height: 38px

    &__link 
      font-size: 12px


@media screen and (max-width: 680px)
  .header
    &__nav
      border: none
      justify-content: flex-end

    &__links
      display: none

    &__links-item 
      border-bottom: 1px solid $white

      &:first-of-type
        border-top: 1px solid $white

      &:last-of-type
        border-bottom: none

    &__link
      width: 100%
      display: block
      background-color: $blue
      text-align: center
      height: 62px
      display: flex
      justify-content: center
      align-items: center

      

    &__btn-menu--open
      background-image: url('img/btn-open.svg')

    &__btn-menu--close
      background-image: url('img/btn-close.svg')
      
    &__links--mobile
      position: absolute
      z-index: 99999
      top: 76px
      left: 0
      width: 100%
      flex-direction: column

.opened .header__links--mobile
  display: block