.footer
  background-color: $black
  display: flex
  justify-content: center
  font-weight: 700
  padding: 30px 50px

  &__inner
    max-width: 1360px
    display: flex
    justify-content: space-between
    gap: 50px

  &__stores
    display: flex
    gap: 18px
    flex-shrink: 0

  &__store-logo
    transition: all 0.3s ease
    opacity: 1

    &:hover
      @media #{$mouse-device}
        opacity: 0.5
    
      &:active
        opacity: 0.5

      &:not(:hover)
        opacity: 1

  &__email
    font-size: 25px
    line-height: 1.2
    color: $white
    margin-top: 0

    &-link
      color: #F68A28

      &:hover
        @media #{$mouse-device}
          text-decoration: none

      &:active
        text-decoration: none


  &__text
    color: $white
    font-size: 16px
    line-height: 1.4
    margin: 0


@media screen and (max-width: 960px)
  .footer
    &__email
      font-size: 14px

    &__text
      font-size: 10px


@media screen and (max-width: 760px)
  .footer
    &__inner
      flex-direction: column-reverse
      justify-content: center
      gap: 20px


@media screen and (max-width: 480px)
  .footer
    padding: 15px