.swiper-container
  box-sizing: border-box
  width: 100%
  position: relative
  margin-top: 50px
  padding-bottom: 90px

.swiper-3d .swiper-slide-shadow
  background: rgba(0, 0, 0, 0)!important


.swiper
  box-sizing: content-box
  display: flex
  justify-content: space-between
  padding-left: 25%
  padding-right: 25%
  max-width: 470px
  margin-left: 0

  &-slide
    width: fit-content!important

    &__img
      height: auto
  
  &-pagination
    position: absolute
    bottom: -50px !important


  &-pagination-bullet
    width: 15px
    height: 15px
    background-color: #524B4B
    border: 2px solid #524B4B
    opacity: 1
    margin: 0px 8px !important
    box-sizing: border-box

    &:hover
      @media #{$mouse-device}
        transition: all 0.3s ease
        background-color: $orange
        border: 2px solid $orange
        transform: scale(1.3)

    &:active
      transition: all 0.3s ease
      background-color: $orange
      border: 2px solid $orange
      transform: scale(1.6)

    &:not(:hover)
      @media #{$mouse-device}
        transition: all 0.3s ease
        transform: scale(1)
        background-color: #777777
        border: 2px solid #777777


    &-active
      width: 15px
      height: 15px
      background-color: $orange
      border: 2px solid $orange
      opacity: 1
      box-sizing: border-box

      &:hover
        @media #{$mouse-device}
          transition: all 0.3s ease
          border: 2px solid $orange

      &:active
        transition: all 0.3s ease
        border: 2px solid $orange

      &:not(:hover)
        @media #{$mouse-device}
          transition: all 0.3s ease
          border: 2px solid $orange
          background-color: $orange

  &-button-prev, 
  &-button-next
    max-width: 100px
    max-height: 100px
    width: 100%
    height: 100%
    box-sizing: border-box
    background-color: $orange
    box-shadow: inset 5px -7px 8px rgba(0,0,0,0.25)
    border-radius: 50%
    top: calc(50% - 50px)

    &::after
      content: ""
      background-image: url(img/arrow-slider.svg)
      background-repeat: no-repeat
      background-size: contain
      background-position: center
      background-position-x: 30%
      width: 70%
      height: 70%

  &-button-next::after
    background-image: url('img/arrow-slider--reverse.svg')
    transform: translate(14%, 0)

  &-button-prev
    left: 0

  &-button-next
    right: 0

    &::after
      transform: translate(14%, 0)

  &-button-prev:hover,
  &-button-next:hover
    @media #{$mouse-device}
      transition: all 0.3s ease
      transform: translate(0, 6px)
      background-color: #E37C1D
      box-shadow: 0px 0px #BF6A1D

    &::after
      transition: all 0.3s ease

  &-button-prev:active,
  &-button-next:active
    transition: all 0.3s ease
      transform: translate(0, 6px)
      background-color: #E37C1D
      box-shadow: 0px 0px #BF6A1D

    &::after
      transition: all 0.3s ease
      text-shadow: 0px 0px #BF6A1D

  &-button-prev:not(:hover),
  &-button-next:not(:hover)
    @media #{$mouse-device}
      transition: all 0.3s ease
      transform: translate(0, 0px)
      background-color: $orange
      box-shadow: inset 5px -7px 8px rgba(0,0,0,0.25)

    &::after
      transition: all 0.3s ease
      text-shadow: 0px 2px #BF6A1D


@media(max-width: 1600px)
  .swiper
    &-button-prev, 
    &-button-next
      width: 60px
      height: 60px

@media(max-width: 580px)
  .swiper

    &-button-prev
      left: -85px

    &-button-next
      right: -85px

@media(max-width: 440px)
  .swiper

    &-button-prev,
    &-button-next
      display: none
