.toke
  &__inner
    padding: 90px 50px 90px 50px
    max-width: 1700px
    box-sizing: content-box
    margin: auto

  &__columns
    display: flex
    gap: 60px
    align-items: flex-start

  &__h2
    font-size: $h2-size
    text-align: center
    font-weight: 400
    margin: 0
    margin-bottom: 90px

  &__h3
    font-size: 54px
    margin: 0
    margin-top: 90px
    margin-bottom: 60px
    text-align: center

  &__h4
    font-size: 32px
    font-weight: 700
    margin: 0
    margin-top: 48px

  &__p
    font-size: 32px
    margin: 0
    margin-top: 24px
  
  &__ol
    margin-left: 12px

  &__li
    font-size: 32px
    margin-top: 24px

  &__chart
    display: flex
    justify-content: space-between
    align-items: center
    gap: 80px
    margin-top: 32px

  &__chart  &__ul
    padding-left: 90px
    list-style-type: none
    margin: 0

  &__chart &__li 
    font-size: 32px
    position: relative

    &::before
      content: ''
      position: absolute
      top: 0
      left: -90px
      width: 50px
      height: 50px
      border: 1px solid black
  
  &__chart &__li:nth-of-type(1)::before
    background-color: #F68A28
    
  &__chart &__li:nth-of-type(2)::before
    background-color: #FEA500
  
  &__chart &__li:nth-of-type(3)::before
    background-color: #FFCC66

  &__distribution
    margin: auto
    max-width: 1430px




@media screen and (max-width: 1600px)
  .toke
    &__inner
      max-width: 1240px
      box-sizing: border-box

    &__h2 
      font-size: 60px
      margin-bottom: 40px
      
    &__h3
      font-size: 32px
      margin-bottom: 32px

    &__h2 + &__h3
      margin-top: 0

    &__h4
      font-size: 24px
      margin-top: 42px
      margin-bottom: 10px

    &__p
      font-size: 21px
      line-height: 1.35
      margin-top: 0px

    &__li
      font-size: 21px
      margin-top: 18px



@media screen and (max-width: 1210px)
  .toke

    &__chart
      gap: 5%

    &__chart &__ul
      padding-left: 60px
  
    &__chart &__li
      font-size: 21px
    
    &__chart &__li::before
      width: 40px
      height: 40px
      left: -60px

    &__img
      width: 45%
      height: auto

@media screen and (max-width: 960px)
  .toke
    &__columns
      gap: 60px


@media screen and (max-width: 760px)
  .toke

    &__chart
      gap: 30px

    &__chart &__ul
      padding-left: 40px

    &__chart &__li
        font-size: 18px

    &__chart &__li::before
      width: 30px
      height: 30px
      left: -45px
