//BASIC COLORS
$white: #F9F1EB
$black: #000000
$orange: #F68A28
$blue: #21B1DF
$blue--light: #A4DFF2


//SHADOWS
$text-shadow: 0 4px 4px rgba(0,0,0,0.1)

//GRADIENTS
$grad: linear-gradient(to right top, rgba(4,163,137,1), rgba(72,39,123,1))
$grad-70: linear-gradient(to right top, rgba(4,163,137,0.7), rgba(72,39,123,0.7))
$grad-light:  linear-gradient(to right top, rgba(152,125,232,1), rgba(64,239,189,1))


//COLORS THEME
$color__text: $black
$color__text-alt: $white

$bg-color: #EFE8E8
$color__line: $blue--light

$color__accent: $orange
$color__second: $black


//FONTS
$unb: "Unbounded", "Arial", sans-serif

$extralight: 200
$light: 300
$regular: 400
$medium: 500
$semibold: 600
$bold: 700
$extrabold: 800
$black-weight: 900

//HEADING SIZES
$h1-size: 46px
$h2-size: 100px
$h3-size: 40px
$h4-size: 32px
$p-size: 32px

$h2-1024: 64px
$h3-1024: 32px
$p-1024: 21px

$h2-768: 54px
$h3-768: 32px
$h4-768: 32px
$p-768: 24px

$h2-480: 20px
$h3-480: 15px
$h4-480: 10px
$p-480: 10px

$h2-420: 36px

$h2-380: 32px

$p-lh: 32px

//SIZES
$max-width: 1220px


//PADDINGS
$pad: 30px
$pad-mobile: 15px


//MARGINS



//VIEWPORTS
$tablet-width: 768px
$desktop-width: 1200px
$mobile-width-only: 767px

$mouse-device: '(hover: hover) and (pointer: fine)'