.hero
  position: relative
  background-color: #21B1DF
  background-image: url('img/skyscraper.svg')
  background-repeat: no-repeat
  background-position: bottom left
  background-position-x: calc(50% - 445px)
  background-size: contain
  padding: 0 140px 0 30px
  

  &__inner
    display: flex
    flex-direction: row
    align-items: center
    gap: 35px
    height: 610px

  &__img-wrapper
    width: 50%
    height: 100%
    display: flex
    justify-content: flex-end
    align-items: flex-end
  
  &__img
    width: 335px
    height: auto


  &__text-wrapper
    width: 45%
    display: flex
    flex-direction: column


  &__h1
    font-size: 44px
    text-transform: uppercase
    font-weight: 900
    margin: 0
    margin-bottom: 36px
    max-width: 790px
    line-height: 125%

  &__socials
    position: absolute
    z-index: 9999
    bottom: 30px
    right: 32px


@media screen and (max-width: 1920px)
  .hero
    &__inner
      height: 520px

    &__img
      width: 295px

    &__h1
      font-size: 34px
      

@media screen and (max-width: 1420px)
  .hero
    &__h1
      max-width: 790px
      font-size: 36px


@media screen and (max-width: 1210px)
  .hero
    &__h1
      font-size: 32px
    



@media screen and (max-width: 1100px)
  .hero
    background-position: -95px
    
    &__inner
      gap: 20px
      height: 490px

    &__h1
      font-size: 30px
      margin-bottom: 32px

    &__img
      max-width: 360px
      height: auto


@media screen and (max-width: 960px)
