.games
  &__inner
    padding: 90px 50px 90px 50px
    max-width: 1700px
    margin: auto
    box-sizing: border-box

  &__h2
    font-size: $h2-size
    text-align: center
    font-weight: 400
    margin: 0
    margin-bottom: 90px

  &__video-wrapper
    margin-top: 50px
    margin-bottom: 90px
    display: flex
    justify-content: center
    align-items: center
    position: relative
    z-index: 0
    padding-left: 25%
    padding-right: 25%

  &__p
    font-size: 32px
    margin: 0
    margin-top: 24px

  &__video
    height: auto

  &__btn-play
    position: absolute
    width: 40%
    height: 45%
    background-repeat: no-repeat
    background-size: contain
    position: absolute
    z-index: 99999
    transition: all 0.3s ease
    border: none
    background-color: transparent

    &:hover
      @media #{$mouse-device}
        transition: all 0.3s ease
        transform: scale(1.1)
        cursor: pointer

    &:active
      transition: all 0.3s ease
      transform: scale(1.1)
      cursor: pointer
      
    &:nth-of-type(1)
      background-image: url('img/btn__play--white.svg')


  &__columns
    display: flex
    justify-content: space-between
    gap: 100px

  &__column
    width: 50%
    display: flex
    flex-direction: column
    justify-content: space-between

  &__h3
    font-size: 54px
    margin: 0
    margin-bottom: 40px

  &__h4
    font-size: 40px
    font-weight: 700
    margin: 0
    margin-top: 60px
    margin-bottom: 22px

  &__p,
  &__dl
  &__dt
    font-size: 32px
    margin: 0
    margin-top: 24px

  &__h4 + &__p
    margin-top: 0px

  &__li
    margin-top: 24px

    &:first-of-type
      margin-top: 0

  &__mini-game
    margin-top: 100px

  &__mini-game &__h3
    text-align: center

  &__dl-li
    margin-top: 24px

  &__dt
    display: inline
    font-weight: 700
    font-size: 32px
    line-height: 1.15
    margin: 0
  
  &__dd
    display: inline
    font-weight: 400
    font-size: 32px
    line-height: 1.15
    margin: 0


  &__screenshot
    display: flex
    gap: 6%
    justify-content: space-between
    align-content: center
    margin: 60px auto
    max-width: 90%
    

  &__phone-wrapper
    position: relative
    z-index: 0
    background-image: url('img/phone.svg')
    background-repeat: no-repeat
    background-size: 100% 100%
    max-width: 426px
    width: 100%
    aspect-ratio: 426 / 741

  &__screen
    position: absolute
    top: -1px
    left: 0
    z-index: 999
    max-width: 426px
    width: 100%
    height: 100%
    aspect-ratio: 426 / 741


@media screen and (max-width: 1600px)
  .games
    &__inner
      max-width: 1240px

    &__mini-game
      margin-top: 60px

    &__h2 
      font-size: 60px
      margin-bottom: 40px
      
    &__h3
      font-size: 32px
      margin-bottom: 12px

    &__h4
      font-size: 24px
      margin-top: 42px
      margin-bottom: 10px

    &__p,
    &__dt,
    &__dd
      font-size: 21px
      line-height: 1.35
      margin-top: 0px

    &__dl
      margin-top: 0px
      margin-bottom: 0px

    &__columns
      gap: 100px

    &__ol
      margin-top: 10px

    &__screenshot
      max-width: 90%

  

@media screen and (max-width: 1024px)
  .games
    &__columns
      gap: 120px

    &__h2
      font-size: $h2-1024
      margin-bottom: 60px

    &__h3
      font-size: 32px
    
    &__h4
      margin-bottom: 10px

    &__p
      font-size: $p-1024
      margin-bottom: 40px

    &__dl-li
    margin-top: 18px

    &__dt,
    &__dd
      font-size: $p-1024

    &__screenshot 
      margin: 40px 0


@media screen and (max-width: 760px)
  .games
    &__inner
      padding: 50px 30px

    &__h2
      font-size: $h2-768
      margin-top: 0
      margin-bottom: 42px

    &__p
      font-size: $p-768
      margin-top: 12px
      margin-bottom: 42px

    &__screen
      border-radius: 0

    &__columns
      flex-direction: column
      gap: 48px

      & .games__p:last-of-type
        margin-bottom: 0

    &__dl-li
      margin-top: 18px

    &__dt,
    &__dd
      font-size: $p-768


  
@media screen and (max-width: 480px)
  .games
    &__inner
      padding: 10px 10px 34px 10px

    &__screen
      border-radius: 0

    &__h2
      font-size: $h2-480
      margin-top: 12px
      margin-bottom: 12px
    
    &__h3
      font-size: 15px
      margin-top: 24px

    &__h4
      font-size: 12px
      margin-top: 18px

    &__p
      font-size: $p-480
      margin-bottom: 18px
      line-height: 12px

    &__ul,
    &__ol
      padding-left: 15px

    &__screenshot
      margin: 20px 0

    &__mini-game
      margin-top: 30px

    &__dl-li
    margin-top: 14px
    line-height: 10px

    &__dt,
    &__dd
      font-size: $p-480

    &__dl
      margin-bottom: 0

    &__columns
      gap: 0

    &__li
      margin-top: 12px