.btn
  position: relative
  width: auto
  display: flex
  justify-content: center
  align-items: center
  text-align: center
  font-size: 32px
  color: $white
  text-decoration: none
  font-weight: 700
  background-color: #F68A28
  border-radius: 12px
  padding: 33px 42px 33px 98px
  max-width: 410px
  box-shadow: inset 5px -7px 8px rgba(0,0,0,0.25), 0 0 30px 20px rgba(256, 256, 256, 0)
  background-image: url('img/btn-play.svg')
  background-size: 42px
  background-position: 50px
  background-repeat: no-repeat
  transition: all 0.3s ease
  

  &:hover
    @media #{$mouse-device}
      transform: scale(1.08)
      box-shadow: inset 5px -7px 8px rgba(0,0,0,0.25), 0 0 30px 20px rgba(256, 256, 256, 0.6)
      
  &:hover::after
    @media #{$mouse-device}


  &:active



  &:not(:hover)


@media screen and (max-width: 1920px)
